const INITIAL_STATE = {
  exercice: "SQUATS",
  interval: 8,
  currentInterval: 1,
  active: 20,
  currentActive: 20,
  passive: 10,
  currentPassive: 10,
  rest: 60,
  currentRest: 60,
  lap: 4,
  currentLap: 1,
};

function counterReducer(state = INITIAL_STATE, action) {
  //////////////////////// active timer
  switch (action.type) {
    case "ACTIVE-TIMER":
      return {
        ...state,
        currentActive: state.currentActive - 1,
      };
    ///////////////////////// reset

    case "RESET":
      console.log(state.active);
      return {
        ...state,
        currentActive: state.active,
      };

    //////////////////////   interval
    case "MORE-INT":
      if (state.interval < 20) {
        return {
          ...state,
          interval: state.interval + 1,
          currentInterval: state.currentInterval + 1,
        };
      } else {
        return {
          ...state,
          interval: state.interval,
          currentInterval: state.currentInterval,
        };
      }
    case "LESS-INT":
      if (state.interval > 1) {
        return {
          ...state,
          interval: state.interval - 1,
          currentInterval: state.currentInterval - 1,
        };
      }
      return {
        ...state,
        interval: state.interval,
        currentInterval: state.currentInterval,
      };

    ///////////////////////   active

    case "MORE-ACT":
      if (state.active < 60) {
        return {
          ...state,
          active: state.active + 5,
          currentActive: state.currentActive + 5,
        };
      } else {
        return {
          ...state,
          active: state.active,
          currentActive: state.currentActive,
        };
      }
    case "LESS-ACT":
      if (state.active > 5) {
        return {
          ...state,
          active: state.active - 5,
          currentActive: state.currentActive - 5,
        };
      } else {
        return {
          ...state,
          active: state.active,
          currentActive: state.currentActive,
        };
      }

    /////////////////////// passive
    case "MORE-PAS":
      if (state.passive < 60) {
        return {
          ...state,
          passive: state.passive + 5,
        };
      } else {
        return {
          ...state,
          passive: state.passive,
        };
      }
    case "LESS-PAS":
      if (state.passive > 4) {
        return {
          ...state,
          passive: state.passive - 5,
        };
      } else {
        return {
          ...state,
          passive: state.passive,
        };
      }

    /////////////////////// rest
    case "MORE-RES":
      if (state.rest < 300) {
        return {
          ...state,
          rest: state.rest + 10,
        };
      } else {
        return {
          ...state,
          rest: state.rest,
        };
      }
    case "LESS-RES":
      if (state.rest > 9) {
        return {
          ...state,
          rest: state.rest - 10,
        };
      } else {
        return {
          ...state,
          rest: state.rest,
        };
      }

    /////////////////////// lap
    case "MORE-LAP":
      if (state.lap < 20) {
        return {
          ...state,
          lap: state.lap + 1,
        };
      } else {
        return {
          ...state,
          lap: state.lap,
        };
      }
    case "LESS-LAP":
      if (state.lap > 1) {
        return {
          ...state,
          lap: state.lap - 1,
        };
      } else {
        return {
          ...state,
          lap: state.lap,
        };
      }
  }

  return state;
}

export default counterReducer;
