import React, { useState, useEffect } from "react";
import { useSelector, useDispatch } from "react-redux";

export default function Counter() {
  const exercice = useSelector((state) => state.exercice);
  const interval = useSelector((state) => state.interval);
  const currentInterval = useSelector((state) => state.currentInterval);
  const active = useSelector((state) => state.active);
  const currentActive = useSelector((state) => state.currentActive);
  const passive = useSelector((state) => state.passive);
  const currentPassive = useSelector((state) => state.currentPassive);
  const rest = useSelector((state) => state.rest);
  const currentRest = useSelector((state) => state.currentRest);
  const lap = useSelector((state) => state.lap);
  const currentLap = useSelector((state) => state.currentLap);

  const [play, setPlay] = useState(false);

  const dispatch = useDispatch();

  const activeTimer = () => {
    setPlay(!play);
  };

  const resetTimer = () => {
    const progressBar = document.querySelector(".current-progress");
    progressBar.style.width = `100%`;
    setPlay(false);
    dispatch({
      type: "RESET",
    });
  };

  useEffect(() => {
    let id;
    if (play) {
      id = window.setInterval(() => {
        dispatch({
          type: "ACTIVE-TIMER",
        });
      }, 1000);
    }

    return () => {
      window.clearInterval(id);
    };
  }, [play]);

  useEffect(() => {
    const progressBar = document.querySelector(".current-progress");
    if (play) {
      progressBar.style.width = `${(currentActive * 100) / active}%`;
      setInterval(() => {}, 1000);
    }
  }, [currentActive]);

  return (
    <div className="container-counter">
      <div className="exercice">{exercice}</div>
      <div className="timers">
        <div className="timer-active primary">
          <em>{currentActive}</em>/{active}s
        </div>

        <div className="timer-passive secondary">
          <em>{currentPassive}</em>/{passive}s
        </div>
      </div>
      <div className="laps">
        <div className="current-interval">
          intervalle : <em>{currentInterval}</em> / {interval}
        </div>
        <div className="current-lap">
          tour : <em>{currentLap}</em> / {lap}
        </div>
      </div>
      <div className="progress-bar">
        <div className="current-progress"></div>
      </div>
      <div className="buttons">
        <button onClick={activeTimer}>
          {play ? (
            <i className="fas fa-pause"></i>
          ) : (
            <i className="fas fa-play"></i>
          )}
        </button>
        <button onClick={resetTimer}>
          <i className="fas fa-redo-alt"></i>
        </button>
      </div>
    </div>
  );
}
